* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
}

.practice {
  height: auto;
  width: 100%;
  background-color: rgb(241, 236, 236);
  position: relative;
}
.practice .P_heading {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}
.card1 {
  display: flex;
  flex-direction: row;
}
.card {
  text-align: center;
}

.gallery {
  display: flex;
  flex-direction: row;
}
#red {
  color: red;
}
.greetings {
  font-style: italic;
}
nav {
  height: 10vh;
  width: 100%;
  /* position: fixed; */
}
.navbar {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
nav li {
  display: inline-block;
  line-height: 10vh;
}
a {
  text-decoration: none;
  font-size: 1.5rem;
  color: #000;
  font-weight: 700;
}
nav li a {
  text-decoration: none;
  margin-left: 80px;
}
.line {
  border-bottom: 2px solid #000;
}
.hero {
  height: 90vh;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  place-items: center;
  /* margin: 50px auto; */
}
.left {
  margin-top: -50px;
}
.hero h1 {
  font-size: 3.1rem;
  line-height: 100px;
}
.hero img {
  height: 400px;
  width: 400px;
  border-radius: 50%;
  /* object-fit: cover; */
}
/* .title {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
} */
h1 {
  margin-top: 30px;
}
/* hr {
  background-color: red;
} */
.about {
  height: 100vh;
  width: 100%;
  /* background-color: lightgray; */
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  background-color: lightgray;
}
p {
  font-size: 2rem;
  text-align: justify;
  margin-top: 100px;
  display: flex;
  flex-wrap: wrap;
  padding: 0 300px;
}
.Projects {
  height: 90vh;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  padding: 40px;
  padding-top: 60px;
}
.Projects img {
  height: 180px;
  width: auto;
  object-fit: cover;
  border: 1px solid #000;
  /* margin: 20px; */
}
.contact {
  height: 90vh;
  width: 100%;
  position: relative;
}
form {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  place-items: center;
  display: grid;
  grid-template-columns: 1fr;
}
input,
textarea {
  height: 50px;
  width: 400px;
  margin: 20px;
  padding-left: 20px;
  font-size: 1rem;
}
textarea {
  height: 150px;
  font-size: 1.5rem;
  padding-top: 20px;
}
.C-btn {
  height: 50px;
  width: 100px;
  margin-top: 20px;
  margin-left: -300px;
  /* grid-column: 1/1; */
  /* float: left; */
  font-size: 1rem;
  cursor: pointer;
}
.practice .img {
  height: 300px;
  width: 700px;
  object-fit: cover;
}
.heading_link {
  margin: 40px;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-wrap: wrap;
}
.heading_link a {
  margin: 10px 20px;
  border: 2px solid #000;
  padding: 10px;
}
.center_div {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.back {
  margin: 40px;
}
.back a {
  padding: 5px 20px;
  border: 2px solid #000;
}
.IncDecNum {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: auto;
  width: 350px;
  /* background-color: red; */
  box-shadow: 2px 2px 10px #000;
  border-radius: 20px;
  padding: 20px 0;
  border: 2px solid #30336b;
}
.IncDecNum h1 {
  width: 100%;
  background-color: #30336b;
  text-align: center;
  padding: 10px 0;
  color: #fff;
}
.IncDecNum button {
  padding: 15px 15px;
  margin: 60px 10px 20px 10px;
  border: none;
  box-shadow: 2px 2px 10px #ccc;
  font-size: 1rem;
  color: #fff;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 700;
  letter-spacing: 2px;
  outline: none;
}

.IncDecNum .btn1 {
  background-color: #4834d4;
}
.IncDecNum .btn2 {
  background-color: #22a6b3;
}
